// Import dependencies
import { unsafeCSS, css, html, LitElement } from 'lit';
import { customElement } from 'lit/decorators.js';
import { repeat } from 'lit/directives/repeat.js';

// Banner content
import bannerContents from './banner-contents';

// Import style for web component
import styles from './web-component-style.scss';

// Import images
import logoHorizontal from './img/logo-horizontal.svg';

/**
 * Web component representing the banner of the upgrade page
 *
 * @customElement
 * @augments LitElement
 */
@customElement( 'uap-page-upgrade-banner' )
export class AutomatorUpgradeBanner extends LitElement {
	// Define style
	static styles = css`${ unsafeCSS( styles ) }`;

	/**
	 * Renders the component
	 *
	 * @return {TemplateResult|undefined} The template
	 */
	render() {
		return html`
			<div class="banner">
				${ this._templateBackground }
				${ this._templateContent }
			</div>
		`;
	}

	/**
	 * Returns the template for the content
	 *
	 * @return {TemplateResult|undefined} The template
	 */
	get _templateContent() {
		return html`
			<div class="banner-content">
				<div class="banner-content-logo">	
					<img src="${ logoHorizontal }"/>
				</div>

				<h1
					class="banner-content-title"
				>
					${ wp.i18n._x( "Get Automator's full potential with Pro", 'Upgrade', 'uncanny-automator' ) }
				</h1>

				<div class="banner-content-description">
					<uo-markdown 
						allowed-tags="strong"
						.content=${ wp.i18n._x( 'Get **unlimited** integrations, triggers, actions, and apps. Enjoy **premium support**, early **access to features**, and more powerful benefits!', 'Upgrade', 'uncanny-automator' ) }>
					</uo-markdown>
				</div>

				<div class="banner-content-cta">
					${ this._templateCTAButton }
				</div>
			</div>
		`;
	}

	/**
	 * Returns the template for the CTA
	 *
	 * @return {TemplateResult|undefined} The template
	 */
	get _templateCTAButton() {
		// If the current plan is "elite", don't show the CTA, because they already have access to all features
		switch ( this.currentTierName ) {
			case 'elite':
				return html`
					<uo-button
						color="primary"
						href=${ UncannyAutomatorBackend._site.automator.url_upgrade_to_pro }
						target="_blank"
					>
						${ wp.i18n._x( 'See plans', 'Upgrade', 'uncanny-automator' ) } 
						<uo-icon id="external-link" icon-style="solid"></uo-icon>
					</uo-button>

					<div class="banner-content-cta-disclaimer">
						 ${ wp.i18n._x( "You're already on the best plan!", 'Upgrade', 'uncanny-automator' ) } 
					</div>
				`;

			case 'lite':
				return html`
					<uo-button
						color="success"
						href=${ UncannyAutomatorBackend._site.automator.url_upgrade_to_pro }
						target="_blank"
					>
						${ wp.i18n._x( 'Upgrade to Pro', 'Upgrade', 'uncanny-automator' ) } 
						<uo-icon id="external-link" icon-style="solid"></uo-icon>
					</uo-button>
				`;

			default:
				return html`
					<uo-button
						color="success"
						href=${ UncannyAutomatorBackend._site.automator.url_upgrade_to_pro }
						target="_blank"
					>
						${ wp.i18n._x( 'Upgrade plan', 'Upgrade', 'uncanny-automator' ) } 
						<uo-icon id="external-link" icon-style="solid"></uo-icon>
					</uo-button>
				`;
		}
	}

	/**
	 * Returns the template for the background
	 *
	 * @return {TemplateResult|undefined} The template
	 */
	get _templateBackground() {
		return html`
			<div class="banner-background">
				<div class="banner-background-items">
					${
						repeat(
							this.bannerItems,
							item => item.id,
							item => this.templateBackgroundItem( item )
						)
					}
				</div>
			</div>
		`;
	}

	/**
	 * Returns the template for the background item
	 *
	 * @param {Object} item The item
	 * @param {String} item.id The item ID
	 * @param {String} item.icon The item icon
	 * @param {String} item.title The item title
	 * @return {TemplateResult|undefined} The template
	 */
	templateBackgroundItem( item ) {
		return html`
			<div class="banner-background-item">
				<div class="banner-background-item-icon">
					<uo-icon 
						id=${ item.icon }
						icon-style="solid"
					></uo-icon>
				</div>

				${ item.title }
			</div>
		`;
	}

	/**
	 * Returns the banner items in random order
	 *
	 * @return {Array} The banner items in random order
	 */
	get bannerItems() {
		return bannerContents.items.sort( () => 0.5 - Math.random() );
	}

	/**
	 * Gets the current plan ID.
	 *
	 * @return {String} The current plan ID.
	 */
	get currentTierName() {
		return UncannyAutomatorBackend._site.automator.plan_details.plan;
	}
}
